import React, { useState } from 'react'
import { TouchableWithoutFeedback, StyleSheet, View } from 'react-native'
import { Button, Icon, IndexPath, Input, Select, SelectItem, Text } from '@ui-kitten/components'
import * as Clipboard from 'expo-clipboard'

import TetherInvoice from '../components/TetherInvoice'
import { SUPPORT_PAYMENTS } from '../helpers/constants'

const AlertIcon = (props) => (
  // <Icon {...props} name='alert-circle-outline'/>
  <Text>{'Alert'}</Text>
)

export default () => {
  const [selectedIndex, setSelectedIndex] = useState(new IndexPath(0))
  const [address, setAddress] = useState('')
  const [amount, setAmount] = useState(10)
  const [showInvoice, setShowInvoice] = useState(false)
  const displayValue = SUPPORT_PAYMENTS[selectedIndex?.row||0]?.name

  const handlePaste = async () => {
    const text = await Clipboard.getStringAsync()
    setAddress(text)
  }

  const renderIcon = (props) => (
    <TouchableWithoutFeedback onPress={handlePaste}>
      {/* <Icon {...props} name={'clipboard-outline'}/> */}
      <Text>{'Paste'}</Text>
    </TouchableWithoutFeedback>
  )

  // const renderCaption = () => {
  //   return (
  //     <View style={styles.captionContainer}>
  //       {AlertIcon(styles.captionIcon)}
  //       <Text style={styles.captionText}>Should contain at least 8 symbols</Text>
  //     </View>
  //   )
  // }

  const generateInvoice = () => {
    setShowInvoice(true)
  }

  return (
    <View>
      <Select
        label='Token - Chain'
        selectedIndex={selectedIndex}
        value={displayValue}
        onSelect={setSelectedIndex}
        style={styles.field}>
        {SUPPORT_PAYMENTS.map((item) => (
          <SelectItem title={item.name} key={item.symbol} />
        ))}
      </Select>
      <Input
        value={address}
        label='Address'
        placeholder='Place your Address'
        // caption={renderCaption}
        accessoryRight={renderIcon}
        onChangeText={setAddress}
        style={styles.field}
      />
      <Input
        value={amount}
        label='Amount'
        placeholder='10'
        // caption={renderCaption}
        // accessoryRight={renderIcon}
        onChangeText={setAmount}
        style={styles.field}
      />
      <Button onPress={generateInvoice} style={styles.button}>
        Generate Invoice
      </Button>
      {showInvoice &&
      <TetherInvoice
        symbol={SUPPORT_PAYMENTS[selectedIndex?.row||0].symbol}
        address={address}
        amount={amount}
      />}
    </View>
  )
}

const styles = StyleSheet.create({
  captionContainer: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
  },
  captionIcon: {
    width: 10,
    height: 10,
    marginRight: 5
  },
  captionText: {
    fontSize: 12,
    fontWeight: "400",
    fontFamily: "opensans-regular",
    color: "#8F9BB3",
  },
  field: {
    paddingVertical: 8,
  },
  button: {
    paddingVertical: 16,
    marginVertical: 8,
  },
})
