import React, { useState, useEffect } from 'react'
import { TouchableWithoutFeedback, View, StyleSheet } from 'react-native'
import { Input, Layout, Popover, Text } from '@ui-kitten/components'
import QRCode from 'react-native-qrcode-svg'
import * as Clipboard from 'expo-clipboard'

export default ({ symbol, address, amount }) => {
  const [invoice, setInvoice] = useState('')
  const [showCopied, setShowCopied] = useState(false)

  useEffect(() => {
    setInvoice(`${symbol}:${address}?amount=${amount}`)
  }, [symbol, address, amount])

  if (invoice === '') {
    return null
  }

  const handleCopy = () => {
    Clipboard.setString(invoice)
    setShowCopied(true)
  }

  const renderIcon = (props) => (
    <TouchableWithoutFeedback onPress={handleCopy}>
      <Text>{'Copy'}</Text>
    </TouchableWithoutFeedback>
  )

  const renderInvoice = () => (
    <Input
      value={invoice}
      label='Invoice'
      accessoryRight={renderIcon}
      style={styles.input}
    />
  )

  return (
    <View style={styles.container}>
      <QRCode value={invoice} size={280} />
      <Popover
        backdropStyle={styles.backdrop}
        visible={showCopied}
        anchor={renderInvoice}
        onBackdropPress={() => setShowCopied(false)}>
        <Layout style={styles.content}>
          <Text>
            Invoice copied to Clipboard
          </Text>
        </Layout>
      </Popover>
    </View>
  )
}

const styles = StyleSheet.create({
  container: {
    paddingVertical: 16,
  },
  input: {
    paddingVertical: 8,
  },
  content: {
    flexDirection: 'row',
    alignItems: 'center',
    paddingHorizontal: 4,
    paddingVertical: 8,
  },
  backdrop: {
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
  },
})
